import { theme } from "@rp/react/theme"
import { Button } from "@rp/react/web/mui/core"
import { Box, CircularProgress } from "@rp/react/web/mui/core"

type Props = {
  submitLabel: string
  isLoading: boolean
  submitDisabled: boolean
  backButtonLabel?: string
  handleBackButton: () => void
}

function FormButtons({
  submitLabel,
  isLoading,
  submitDisabled,
  handleBackButton,
  backButtonLabel = "Back To Login",
}: Props) {
  return (
    <Box
      sx={{
        "& button": { fontSize: 16, marginTop: theme.spacing(2) },
      }}
    >
      {isLoading ? (
        <Box sx={{ display: "flex", justifyContent: "center", width: "100%", marginTop: 4 }}>
          <CircularProgress size={36} />
        </Box>
      ) : (
        <>
          <Button fullWidth variant="contained" type="submit" disabled={submitDisabled}>
            {submitLabel}
          </Button>
          <Button color="primary" type="button" fullWidth onClick={handleBackButton}>
            {backButtonLabel}
          </Button>
        </>
      )}
    </Box>
  )
}

export { FormButtons }
